export default {
	data () {
		return {
			txt: '',
			Description: '',
			show: false,
			DrId: this.$store.state.userInfo == null ? 18 : this.$store.state.userInfo.Data.User.DoctorId,
			page: 1,
			size: 10,
			arr: '',
			addPatListStart: false,
			haveMore: true,
			Total: '',
			checkedNames: [],
			nameStart: 'show',
			show3: false,
			tit:''
		}
	},
	components:{
	},
	created(){
		if(this.$route.query.Diff == 2){
			this.txt = this.$route.query.MFItemName
			this.Description = this.$route.query.Description
			if(this.Description!= "null"){
				this.Description = this.$route.query.Description
			}else{
				this.Description = ""
			}
			
		}
		if(this.$route.query.Diff == 1){
			this.tit = "新建分组"
		}else{
			this.tit = "编辑分组"
		}
	},
	methods: {
		httpStart () {
			if(this.Description.length > 200){
				return this.$toast('您输入的描述文字过长')
			}
			let obj = {}
			obj.url = '/PlatFormAPI/Memberfeatures/AddMemberfeaturesItem'
			if(this.$route.query.Diff == 1){
				obj.parameter = {
					"DrId": this.DrId,
					"MFItemId":"",
					"MFItemName": this.txt,
					"Description": this.Description,
				}
			}else{
				obj.parameter = {
					"DrId": this.DrId,
					"MFItemId":this.$route.query.MFItemId,
					"MFItemName": this.txt,
					"Description": this.Description,
				}
			}
			if(this.txt == ''){
				this.show3 = true
				return
			}
			this.$post(obj.url,obj.parameter).then(o => {
				if(o){
					this.show = true
					let _this = this
					setTimeout(() => {
						_this.$router.back();
					},1100);
				}
			})
		},
		gridItemFn (data,n){
			if(this.arr[n].gridItem == 'circle'){
				this.checkedNames.push(data.PatientId);
				this.arr[n].gridItem = 'success'
			}else{
				this.arr[n].gridItem = 'circle'
				this.checkedNames.map((v,i) => {
					if(this.checkedNames[i] == data.PatientId){
						this.checkedNames.splice(i, 1);
					}
				})
			}
			console.log(this.checkedNames)
		},
		patientSex (start){
			switch (start) {
				case 1:
					return '男'
					break;
				case 2:
					return '女'
					break;
				case 3:
					return '未知'
					break;		
			}
		},
		addPatList (flag) {
			if(this.addPatListStart && !flag){
				this.page = 1
				this.addPatListStart = false
				return
			}else{
				this.addPatListStart = true
			}
			let obj = {}
			obj.url = '/PlatFormAPI/DoctorPatient/QueryDoctorPatientPage'
			obj.parameter = {
				"DrId": this.DrId,
				"DrName": "",
				"PatientId": -1,
				"PatientName": '',
				"RelationStatus": 1,
				"IsDrKey": -1,
				"IsPatientMain": -1,
				"PageIndex": this.page,
				"PageSize": this.size
			}
			this.addPatListStart = true
			this.$post(obj.url,obj.parameter).then(o => {
				if(o.ReturnData == ''  || o.ReturnData == [] || o.ReturnData == undefined){
					if(flag != undefined){
						return 
					}
				}
				this.empgasisNumber = o.Total
				this.Total = o.Total
				if (flag){
					o.ReturnData.forEach(v => {
						v.gridItem = 'circle'
						this.arr.push(v)
					})
				}
				else {
					o = o.ReturnData
					o.map((v,n) => {
						o[n].gridItem = 'circle'
					})
					this.arr = o
				}
			})
		},
		getMore(){ //下拉加载分页
			const el = this.$refs.el
			let timer = null
			
			window.onscroll = () => {
				clearTimeout(timer)
				timer = setTimeout(() => {
					const top = document.documentElement.scrollTop
					|| window.pageYOffset
					|| document.body.scrollTop,
					h = el.scrollHeight,
					wh = window.innerHeight
					if (h - top - wh < 100 && this.haveMore){
						if(this.Total > this.size){
							this.page += 1
							this.addPatList(true)
						}
					}
				}, 100)
			}
		}
	},
	mounted () {
		//this.httpStart(this.$route.params.start);
		this.getMore()
	},
}



